import { CartItems } from "@/app/types/product";
import PaymentForm from "./PaymentForm";
import ProductCard from "./ProductCard";
import ShoppingForm from "./ShoppingForm";

export default function Steps({
  cartItems,
  activeStep,
  shippingForm,
}: {
  cartItems: CartItems[];
  activeStep: number;
  shippingForm: boolean;
}) {
  return (
    <div className="w-full lg:w-7/12 shadow-lg border-1 border-amber-100 p-8 rounded-lg flexflex-col gap-8">
      {activeStep === 1 ? (
        cartItems.map((item: CartItems) => {
          return <ProductCard key={item.id} item={item} />;
        })
      ) : activeStep === 2 ? (
        <ShoppingForm />
      ) : activeStep === 3 && shippingForm ? (
        <PaymentForm />
      ) : (
        <p className="text-sm text-gray-400">
          Please fill the shipping form first
        </p>
      )}
    </div>
  );
}
